@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --text-primary: rgb(211, 211, 211);
  --text-secondary: #1888e4;
}  
* {
  margin: 0;
  padding: 0;
  font-family: 'Bebas Neue', monospace;
  box-sizing: border-box;
}
p, i {
  font-family: 'Courier New', Courier, monospace;
  font-size: 16px;
  font-weight: 600;
}

.nav {
  width: 100px;
  text-align: center;
  border: 2px solid black;
  position: fixed;
  right: 0;
  background-color: #262626;
  display: inline-block;
  background-color: #262626;
  z-index: 9;
}
.container {
  height: 100vh;
}
.home__top {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/Portfolio/static/media/hero.30e69a97.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  height: 100vh;
}


.image-round {
  border-radius: 50%;
  width:80%;
  margin: 10%;
  vertical-align: middle;
  padding: 2%;
  border:2px solid#ccc;
}
#hamburger {
  display: inline-block;
  cursor: pointer;
  margin: auto;
  
}
.bar1,
.bar2,
.bar3 {
  width: 35px;
  height: 5px;
  background-color: #1888e4;
  background-color: var(--text-secondary);
  margin: 6px 0;
  transition: 0.4s;
}

.change .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
  transform: rotate(-45deg) translate(-9px, 6px);
}

.change .bar2 {
  opacity: 0;
}

.change .bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px);
  transform: rotate(45deg) translate(-8px, -8px);
}
.nav-hidden {
  display: none;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.nav__bottom {
  display: block;
  
  padding: 25px 0;
  margin-top: auto;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 20px;
  color: #fff;
  border-bottom: 1px solid #ccc
}

.icon-container {
  display: flex;
  justify-content: space-around;
  width: 100%;
  margin: 5%;
}
.nav-active {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 20%;
  position: fixed;
  top: 0;
  right: 0;
  background-color: #262626;
  display: flex;
  flex-direction: column;
}
.nav-active li {
  list-style: none;
}
.nav-active li a {
  display: block;
  padding: 25px 0;
  margin: auto ;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 2rem;
  color: #fff;
  border-bottom: 1px solid #ccc;
}
.nav-active li a:hover {
  background-color: deepskyblue;
}
.typewriter {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: inline-block;
  color: white;
  position: relative;
  top: 50%;
  left: 30%;
}
.typewriter h3 {
  display: inline-block;
  overflow: hidden;
  border-right: 0.15em solid orange; 
  white-space: nowrap;
  margin: 0 auto; 
  letter-spacing: 0.15em; 
  -webkit-animation: typing 2s steps(40, end), blink-caret 0.75s step-end infinite; 
          animation: typing 2s steps(40, end), blink-caret 0.75s step-end infinite;
  font-family: "Anonymous Pro", monospace;
  font-size: xx-large;
}

/* The typing effect */
@-webkit-keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}


@-webkit-keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: orange;
  }
}


@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: orange;
  }
}
.about__top__flex {
  display: flex;
  justify-content: space-between;
  margin: auto;
}
.about__bold {
  font-family: 'Bebas Neue', monospace;
  font-size: 1.5rem;
}
.about__top__flex p {
  margin :auto;
  width: 45%;
}
.about__top {
  padding: 2%;
  height: 60%;
  color: #262626;
  background-color: #1888e4;
  background-color: var(--text-secondary);
}
.about__top h1 {
  display: inline-block;
  width: 30%;
  border-bottom: 1px solid black;
  margin-bottom: 2%;
}
.about__bottom {
    background-color: rgb(32, 32, 32);
    background-image: linear-gradient(
        45deg,
        black 25%,
        transparent 25%,
        transparent 75%,
        black 75%,
        black
      ),
      linear-gradient(
        45deg,
        black 25%,
        transparent 25%,
        transparent 75%,
        black 75%,
        black
      ),
      linear-gradient(to bottom, rgb(8, 8, 8), rgb(32, 32, 32));
    background-size: 10px 10px, 10px 10px, 10px 5px;
    background-position: 0px 0px, 5px 5px, 0px 0px;
    color: white;
    display: flex;
    justify-content: space-between;
}
.about__bottom p {
  font-size: 1.5rem;
  margin: auto;
}
.about__bottom i {
  color: deepskyblue;
}
.cube__container {
  display: inline-block;
}
.portfolio {
  background-color: #262626;
  height: 120vh;
  justify-content: space-around;
  color: white; 
  padding: 2%;
}
#portfolio__header {
  border-bottom: 2px solid white;
  display: inline-block;
  font-weight: 900;
  font-size: 4rem;
  width: 50%;
}
.projects__container {
  margin-top: 2%;
  display: flex;
}
.gallery__container {
  padding: 5%;
  border: 2px solid white;
  border-bottom: 0px;
  min-height: 35%;
}
.project {
  width: 35%;
  background-color: #262626;
  color: white;
  font-weight: 500;
  max-height: 30%;
  margin-right: 5%;
}
.project p {
  text-align: left;
  font-family: 'Courier New', Courier, monospace;
}
.project__links {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.project__links a {
  border: 2px solid white;
  text-decoration: none;
  color: white;
  text-align: center;
  line-height: 28px;
  padding: 2%;
  width:50%;
  background-color: #1888e4;
}
.project__links a:hover {
  background-color: #2861da
}
.project__text {
  margin: 2%;
}
.project__icons {
  display: grid;
  grid-template-rows: 42px 42px 42px;
  grid-template-columns: 42px 42px 42px;
  grid-column-gap:1rem;
  -webkit-column-gap:1rem;
          column-gap:1rem;
  grid-row-gap: 1rem;
  row-gap: 1rem;
}
#contact input, textarea {
  font-family: 'Courier New', Courier, monospace;
}
@media screen and (max-width: 600px) {
  p {
    font-size: small;
  }
  .home__top {
    height: 100vh;
    width: 100vw;
  }
  .nav {
    background: #262626;
    height: 50px;
    left: 0;
  }
  .nav-active {
    width: 100vw;
    display: block;
    position: absolute;
    left: 0;
  }
  .nav-active li {
    background-color: #262626;
  }
  .nav__bottom {
    display: none;
  }
  .typewriter {
    width: 100vw;
    top: 20%;
    left: 0;
  }
  
  .projects__container {
    display: block;
  }
  .portfolio {
    height: 170vh;
  }
  .about__top {
    height: 60vh;
  }
  .about__top__flex {
    display: block;
    
  }
  .about__top__flex img {
    width: 250px;
  }
  .about__top__flex p {
    width: 100%;

  }
  .about__bottom {
    display: block;
    padding: 4%;
  }
  .about__bottom p {
    font-size: 1rem;
    margin-right: 5%;
  }
  .project {
    width: 100%;
    margin-top: 5%;
  }
  .typewriter h3 {

    font-size: 16px;
  }
  .projects__container {
    display: block;
  }
  .cube__container {
    margin-left: 40px;
  }

}
#contact {
  display: block;
  height: 30vh;
  background-color: #262626;
  padding: 2%;
  color: white;
}
.form {
  display: flex; 
  justify-content: space-around;
}
.form-group {
  width: 30%;
}
.form-group input[type=text] {
  
  display: block;
  width: 100%;
  padding: 2%;
}
.form-group input[type=submit] {
  width: 20%;
  padding: 1%;
  background-color: deepskyblue;
  border-radius: 5%;
  width: 100%;
}
.form-group textarea {
  display: block;
  width: 50%;
}
@media screen and (max-width: 900px) {
  p {
    font-size: small;
  }
  .home__top {
    height: 100vh;
  }
  .nav {
    background: #262626;
    height: 50px;
    left: 0;
  }
  .nav-active {
    width: 100vw;
    display: block;
    position: absolute;
    left: 0;
  }
  .nav-active li {
    background-color: #262626;
  }
  .nav__bottom {
    display: none;
  }
  .typewriter {
    width: 100vw;
    margin: auto;
    top: 20%;
    left: 1%;
  }
  .projects__container {
    display: block;
  }
  .portfolio {
    height: 250vh;
  }
  .about__top {
    height: 80vh;
  }
  .about__top__flex {
    display: block;
    
  }
  .about__top__flex img {
    width: 250px;
  }
  .about__top__flex p {
    width: 100%;

  }
  .about__bottom {
    display: block;
  }
  .about__bottom p {
    font-size: 1rem;
    margin: 4%;
  }
  .project {
    width: 100%;
    margin-top: 5%;
  }
  .typewriter h3 {

    font-size: 16px;
  }
  .projects__container {
    display: block;
  }
  .cube__container {
    margin-left: 40px;
  }

}
* {
    box-sizing: border-box;
}
.cube {
    margin: 200px;
    display: inline-block;
    width: 200px;
    height: 200px;
    -webkit-perspective: 5000px;
    -webkit-perspective-origin: 956% -200%;
    perspective: 5000px;
    perspective-origin: 956% -200%;
}
.cubeAP{
    transform-style: preserve-3d;
    -webkit-transform-style: preserve-3d;
    width: 100%;
    height: 100%;
    animation: rotate 6s infinite linear;
    -webkit-animation: rotate 6s infinite linear;
    
}
.cubeAP div {
    background: #000;
    width: 100%;
    height: 100%;
    position: absolute;
    transform-origin:50% 50%;
    -webkit-transform-origin: 50% 50%;
    border-radius: 2px;
}
.cubeAP div span {
    display: inline-block;
    width: 49px;
    height: 50px;
    margin: 7px;
    border-radius: 3px;
    -webkit-border-radius: 4px;

}
.right {
    -webkit-transform: translateX(-100px) rotateY(90deg);
            transform: translateX(-100px) rotateY(90deg);
}
.up {
    transform: rotateX(90deg) translateZ(100px);
    -webkit-transform: rotateX(90deg) translateZ(100px)
}
.front {
    transform: translateZ(100px);
    -webkit-transform: translateZ(100px);
}
.left {
    transform: translateX(100px) rotateY(90deg);
    -webkit-transform: translateX(100px) rotateY(90deg);
}
.down {
    transform: rotateX(90deg)
    translateZ(-100px);
    -webkit-transform: rotateX(90deg)
    translateZ(-100px);
}
.back {
    transform: translateZ(-100px);
    -webkit-transform: translateZ(-100px);
}
.up span {
    background: #ff8c00;
}
.right span {
    background-color: #0000cd;
}
.front span {
    background: #ff0000;
}
.left span {
    background: #fff5ee;
}
.down span {
    background: #008000;
}
.back span {
     background: #ffd700;
}
@keyframes rotate {
    0%, 100%{
        transform: rotateY(0);
        -webkit-transform: rotateY(0);
    }

    25% {
        transform: rotateX(180deg);
        -webkit-transform: rotateX(180deg);
    }
    50% {
        transform: rotateZ(-180deg);
        -webkit-transform: rotateZ(-180deg);
    }
    75% {
        transform: rotateY(180deg);
        -webkit-transform: rotateY(180deg)
    }
}
@-webkit-keyframes rotate{
    0%, 100%{
        transform: rotateY(0);
        -webkit-transform: rotateY(0);
    }

    25% {
        transform: rotateX(180deg);
        -webkit-transform: rotateX(180deg);
    }
    50% {
        transform: rotateZ(-180deg);
        -webkit-transform: rotateZ(-180deg);
    }
    75% {
        transform: rotateY(180deg);
        -webkit-transform: rotateY(180deg)
    }

   
}
@media screen and (max-width: 600px) {
    .cube {
        margin: 50px;
    }
}
